import React, { FC, useState } from 'react';
import { ExternalLinks, InternalLinks } from '../../../utils/links';
import {
  StyledQuickLinks,
  QuickLinksList,
  QuickLink,
} from './styled';
import { Text } from '../Text';
import CustomLink from '../Link';
import { colors } from '../../../utils/colors';

type QuickLink = {
  title: string;
  description: string;
  linkName: string;
  linkUrl: string;
  isExternal?: boolean;
};

interface Props {
  //   title: string;
  //   description: string;
}

const quickLinks: QuickLink[] = [
  {
    title: 'Getting Started',
    description: 'Fast & Easy API Integration for CSPs',
    linkName: 'Learn more',
    linkUrl: InternalLinks.GettingStarted,
  },
  {
    title: 'API Docs',
    description: 'Developer-friendly API documentation',
    linkName: 'See API docs',
    linkUrl: ExternalLinks.Developers,
    isExternal: true,
  },
  {
    title: 'Pricing',
    description: 'Competitive SMS API pricing starting at $0.005',
    linkName: 'See pricing',
    linkUrl: InternalLinks.Pricing,
  },
];

const QuickLinks: FC<Props> = () => {
  return (
    <StyledQuickLinks>
      <Text.Title.Bold.lg>Quick Links:</Text.Title.Bold.lg>
      <QuickLinksList>
        {quickLinks.map((link) => (
          <QuickLink key={link.linkUrl}>
            <Text.Regular.Bold.sm color={colors.primary.blue}>
              {link.title}
            </Text.Regular.Bold.sm>
            <Text.Regular.Bold.sm>{link.description}</Text.Regular.Bold.sm>
            <CustomLink address={link.linkUrl} arrow color={colors.secondary.orange} external={link.isExternal}>
              {link.linkName}
            </CustomLink>
          </QuickLink>
        ))}
      </QuickLinksList>
    </StyledQuickLinks>
  );
};

export default QuickLinks;
